// Enhanced 3D Globe with better materials
function EnhancedGlobe() {
  const meshRef = useRef<THREE.Mesh>(null);
  const glowRef = useRef<THREE.Mesh>(null);
  const [hovered, setHovered] = useState(false);
  
  useFrame((state) => {
    if (meshRef.current) {
      meshRef.current.rotation.y += hovered ? 0.008 : 0.003;
      meshRef.current.rotation.x = Math.sin(state.clock.elapsedTime * 0.2) * 0.1;
    }
    if (glowRef.current) {
      glowRef.current.rotation.y -= 0.001;
      const scale = 1.15 + Math.sin(state.clock.elapsedTime * 0.5) * 0.05;
      glowRef.current.scale.setScalar(scale);
    }
  });

  return (
    <Float speed={2} rotationIntensity={0.3} floatIntensity={0.4}>
      <group>
        {/* Main sphere with enhanced material */}
        <Sphere 
          ref={meshRef}
          args={[1.2, 256, 256]}
          onPointerEnter={() => setHovered(true)}
          onPointerLeave={() => setHovered(false)}
        >
          <MeshDistortMaterial
            color="#06b6d4"
            attach="material"
            distort={0.5}
            speed={3}
            roughness={0.1}
            metalness={0.95}
            emissive="#0891b2"
            emissiveIntensity={0.8}
          />
        </Sphere>
        
        {/* Outer glow layer */}
        <Sphere ref={glowRef} args={[1.35, 64, 64]}>
          <meshBasicMaterial 
            color="#06b6d4" 
            transparent 
            opacity={0.15}
            side={THREE.BackSide}
          />
        </Sphere>
        
        {/* Inner bright core */}
        <Sphere args={[0.8, 64, 64]}>
          <meshBasicMaterial 
            color="#38bdf8" 
            transparent 
            opacity={0.3}
          />
        </Sphere>
        
        {/* Sparkles around globe */}
        <Sparkles 
          count={100} 
          scale={3} 
          size={2} 
          speed={0.5}
          opacity={0.6}
          color="#06b6d4"
        />
      </group>
    </Float>
  );
}

// 3D Scene with better lighting and effects
function GlobeScene() {
  return (
    <Canvas 
      camera={{ position: [0, 0, 4], fov: 55 }} 
      dpr={[1, 2]}
      gl={{ 
        antialias: true,
        alpha: true,
        powerPreference: 'high-performance'
      }}
    >
      <color attach="background" args={['#000000']} />
      <fog attach="fog" args={['#020617', 8, 20]} />
      
      {/* Enhanced lighting */}
      <ambientLight intensity={0.4} />
      <pointLight position={[10, 10, 10]} intensity={2.5} color="#06b6d4" />
      <pointLight position={[-10, -10, -10]} intensity={1.5} color="#3b82f6" />
      <pointLight position={[0, 15, 0]} intensity={2} color="#8b5cf6" />
      <spotLight 
        position={[0, 5, 5]} 
        intensity={3}
        angle={0.6} 
        penumbra={1}
        color="#06b6d4"
        castShadow
      />
      
      <Suspense fallback={null}>
        <EnhancedGlobe />
        <Stars 
          radius={150} 
          depth={80} 
          count={12000} 
          factor={8} 
          saturation={0} 
          fade 
          speed={2}
        />
        <Environment preset="night" />
      </Suspense>
      
      <OrbitControls 
        enableZoom={false} 
        autoRotate 
        autoRotateSpeed={0.4}
        enablePan={false}
        minPolarAngle={Math.PI / 3}
        maxPolarAngle={Math.PI / 1.5}
        enableDamping
        dampingFactor={0.05}
      />
    </Canvas>
  );
}

